import { HostListener, Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NotifierService } from 'angular-notifier';

// for work with link
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { Task } from '../task';
import { TaskService } from '../task.service';

@Component({
  selector: 'app-task-detail',
  templateUrl: './task-detail.component.html',
  styleUrls: ['./task-detail.component.scss'],
})
export class TaskDetailComponent implements OnInit {

  constructor(
    private route: ActivatedRoute,
    private taskService: TaskService,
    private location: Location,
    notifierService: NotifierService
  ) { this.notifier = notifierService; }

  @Input() task: Task;
  @Output() taskChange: EventEmitter<any> = new EventEmitter<any>();

  private readonly notifier: NotifierService;
  @HostListener('window:keydown', ['$event'])
  handleKeyDown(event: KeyboardEvent) {
    if (event.key === 'Escape') {
      this.onClose();
    }
  }

  ngOnInit(): void { }

  onClose(): void {
    this.task = null;
    this.taskChange.emit(this.task);
  }

  save(task: Task): void {
    this.taskService.updateTask(this.task).subscribe(data => {
      console.log(data);
      // notify
      this.notifier.notify('success', 'Задача успешно обновлена!');
    });
  }
}
